/* Forms */

.input-group {
  padding: 0;
  .input-group-append,
  .input-group-prepend {
    background: color(white);
    color: $input-placeholder-color;
    width: auto;
    border-bottom: 1px solid $input-border-color;
    .input-group-text{
      background: transparent;
      border: 0;
    }
  }
  .input-group-append {
    &:last-child {
      .input-group-text {
        padding-right: 0;    
      }
    }
  }
  .input-group-prepend {
    &:first-child {
      .input-group-text {
        padding-left: 0;    
      }
    }
  }
  .form-control {
    padding: $input-btn-padding-y .75rem;
    &.form-control-lg {
      padding: $input-btn-padding-y-lg .75rem;
    }
    &.form-control-sm {
      padding: $input-btn-padding-y-sm .75rem;
    }
    &:not(last-child) {
      padding-left: 0;
    }
  }
}

.form-control {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  font-family: $type1-regular;
  font-size: $input-font-size;
  padding: $input-btn-padding-y .75rem $input-btn-padding-y 0;
  line-height: 14px;
  &.form-control-lg {
    padding: $input-btn-padding-y-lg .75rem $input-btn-padding-y 0;
  }
  &.form-control-sm {
    padding: $input-btn-padding-y-sm .75rem $input-btn-padding-y 0;
  }
}

select {
  &.form-control {
    padding: .4375rem .75rem;
  }
}
.form-group {
  label {
    font-size: $default-font-size;
    line-height: 1;
    vertical-align: top;
  }
  &.has-danger {
    .form-control {
      border-color: theme-color(danger);
    }
  }
  .file-upload-default {
    visibility: hidden;
    position: absolute;
  }
  .file-upload-info {
    background: transparent;
  }
}
