// Color Functions Mixins


@mixin text-social($color) {
  color: $color !important;
}

// Sider color variations
@mixin slider-color-variant($color) {
  .noUi-base {
    .noUi-connect {
      background: $color;
    }

    .noUi-origin {
      background: $color;

      .noUi-handle {
        &:after {
          background: $color;
        }

        .noUi-tooltip {
          background: $color;
          color: $white;
        }
      }
      @mixin social-button($color) {
        background: $color;
        color: color(white);
      
        &:hover {
          background: darken($color, 10%);
        }
      
        &.btn-link {
          background: none;
          color: $color;
      
          &:hover {
            color: darken($color, 10%);
          }
        }
      }
    }
  }
}
