/* Colorpicker */

.asColorPicker-dropdown {
  max-width: initial;
}
.asColorPicker-trigger{
    height: auto;
    @extend .input-group-prepend;
    padding: 10px;
    border-bottom: 1px solid $border-color;
    border-right: 0;
    border-top: 0;
    border-left: 0;
    span {
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 2px;
    }
}

.asColorPicker-input {
  @extend .form-control;
}

.asColorPicker-wrap {
  @extend .input-group;
}
