/* Sidebar */

.sidebar {
  background: $sidebar-light-bg;
  font-family: $type1-regular;
  min-height: 100%;
  padding: 0;
  position: absolute;
  width: $sidebar-width-lg;
  z-index: 11;
  transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
  -webkit-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
  -moz-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
  -ms-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;

  .nav {
    overflow: hidden;
    flex-wrap: nowrap;
    flex-direction: column;

    .nav-item {
      .collapse {
        z-index: 999;
      }

      .nav-link {
        align-items: center;
        display: flex;
        padding: $sidebar-menu-padding;
        white-space: nowrap;
        height: $nav-link-height;
        color: $sidebar-light-menu-color;

        i {
          color: $sidebar-light-menu-icon-color;

          &.menu-icon {
            margin-right: 1.25rem;
            font-size: $sidebar-icon-font-size;
            line-height: 1;
            &:before {
              vertical-align: middle;
            }
          }

          &.menu-arrow {
            display: inline-block;
            line-height: 13px;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            margin-left: auto;
            margin-right:0;
          }
        }

        .menu-title {
          color: inherit;
          display: inline-block;
          font-size: $sidebar-menu-font-size;
          line-height: 1;
          vertical-align: middle;
        }

        .badge {
          margin-left: auto;
        }

        &:hover {
          color: darken($sidebar-light-menu-color, 5%);
        }
      }

      &.nav-category {
        border-top: 1px solid rgba(color(white), .2);
        border-bottom: 1px solid rgba(color(white), .2);
        color: theme-color(dark);
        font-family: $type1-bold;
        font-size: .625rem;
        line-height: 16px;
        text-transform: uppercase;

        .nav-link {
          padding: $sidebar-category-padding;
          height: auto;        
        }
      }

      &.nav-profile {
        text-align: center;
        font-family: $type1-semibold;
        .nav-link {
          display: block;
          padding: 1rem 2rem;
          height: auto;
        }
        .profile-image {
          width: 70px;
          height: 70px;
          margin: 0 auto 15px;
          img {
              width: 70px;
              height: 70px;
              border-radius: 100%;
          }
        }
        p {
          font-size: 1rem;
          margin-bottom: 0;
        }
        i {
          background: theme-color(light);
          height: 34px;
          width: 34px;
          line-height: 34px;
          color: theme-color(dark);
          border-radius: 100%;
          font-size: 1.25rem;
        }
      }

      &.active {
        > .nav-link {
          background: $sidebar-light-menu-active-bg;
          color: $sidebar-light-menu-active-color;
          .menu-title,
          i {
            color: inherit;
          }
        }
      }
    }
    &:not(.sub-menu) {
      > .nav-item {
        &:hover {
          &:not(.nav-category):not(.nav-profile) {
            > .nav-link {
              background: $sidebar-light-menu-hover-bg;
              color: $sidebar-light-menu-hover-color;
            }
          }
        }
      }
    }
    &.sub-menu {
      margin-bottom: 0;
      padding: $sidebar-submenu-padding;

      .nav-item {
        .nav-link {
          color: $sidebar-light-submenu-color;
          padding: $sidebar-submenu-item-padding;
          font-size: $sidebar-submenu-font-size;
          line-height: 1;
          height: auto;
          position: relative;

          &.active {
            color: darken(theme-color(primary), 50%);
            background: transparent;
          }
        }

        &:hover {
          > .nav-link {
            background: $sidebar-light-submenu-hover-bg;
            color: $sidebar-light-submenu-hover-color;
            &:before {
              background: $sidebar-light-submenu-hover-color;
            }
          }
        }
      }
    }
  }
}
//sidebar color variation
.sidebar-dark {
  background: $sidebar-dark-bg;

  .sidebar {
    background: $sidebar-dark-bg;

    .nav {
      .nav-item {
        .nav-link {
          color: $sidebar-dark-menu-color;
          .menu-title,
          i {
            color: inherit;
          }
        }
        
        &.nav-profile {
          .profile-image {
            .online-status {
              border-color: $sidebar-dark-bg;
            }
          }
          .profile-name {
            .name {
              color: $sidebar-dark-menu-color;
            }
            .designation {
              color: rgba($sidebar-dark-menu-color, .5);
            }
          }
          i {
            color: $sidebar-dark-bg;
          }
        }

        &.nav-progress {
          .nav-progress-sub-heading {
            color: $white;
          }
        }

        &.active {
          > .nav-link {
            background: $sidebar-dark-menu-active-bg;
            color: $sidebar-dark-menu-active-color;
            .menu-title,
            i {
              color: inherit;
            }
          }
        }
      }
      &:not(.sub-menu) {
        > .nav-item {
          &:hover {
            &:not(.nav-profile) {
              > .nav-link {
                background: $sidebar-dark-menu-hover-bg;
                color: $sidebar-dark-menu-hover-color;
              }
            }
          }
        }
      }
      &.sub-menu {
        .nav-item {
          .nav-link {
            color: $sidebar-dark-submenu-color;
            &.active {
              color: darken($sidebar-dark-menu-active-color, 20%);
            }
          }
          &:hover {
            > .nav-link {
              color: $sidebar-dark-submenu-hover-color;
            }
          }
        }
      }
    }
  }
}
/* style for off-canvas menu*/
@media screen and (max-width: 991px) {
  .row-offcanvas {
    position: relative;
    -webkit-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;

    &.row-offcanvas-right {
      right: 0;

      .sidebar-offcanvas {
        right: -100%; // 12 columns
      }

      &.active {
        right: 50%; // 6 columns

        .sidebar-offcanvas {
          right: -50%; // 6 columns
        }
      }
    }

    &.row-offcanvas-left {
      left: 0;

      .sidebar-offcanvas {
        left: -100%; // 12 columns
      }

      &.active {
        left: 50%; // 6 columns

        .sidebar-offcanvas {
          left: -50%; // 6 columns
        }
      }
    }

    .sidebar-offcanvas {
      position: absolute;
      top: 0;
      width: 50%; // 6 columns

      .nav {
        display: block;
      }
    }
  }
}
@media screen and (max-width: 580px) {
  //for smaller devices like iphone 5

  .row-offcanvas {
    &.row-offcanvas-right {
      &.active {
        right: 80%;

        .sidebar-offcanvas {
          right: -75%;
        }
      }
    }

    .sidebar-offcanvas {
      width: 75%;
    }
  }
}
@media screen and (max-width: 350px) {
  //for smaller devices like iphone 5

  .row-offcanvas {
    &.row-offcanvas-right {
      &.active {
        right: 100%;

        .sidebar-offcanvas {
          right: -91%;
        }
      }
    }

    .sidebar-offcanvas {
      width: 91%;
    }
  }
}