// Solid tab variations
@mixin tab-solid-variant($color) {
  .nav-link {
    &.active {
			background: $color;
			box-shadow: 0px 2px 5px 0 rgba($color, .68);
    }
  }
}
// Minimal tab variations
@mixin tab-colored-variant($color) {
  .nav-tabs {
    .nav-item {
      .nav-link {
        &.active {
          color: $color;
        }
      }
    }
  }

  .tab-content {
    border-color: $color;
  }
}