/* Clockpicker */

.bootstrap-datetimepicker-widget {
	&.dropdown-menu {
		width: auto;
		padding: 1.5rem;
		table {
			td {
				width: 34px;
				height: 34px;
				line-height: 34px;
				.timepicker-hour,
				.timepicker-minute,
				span {
					width: 34px;
					height: 34px;
					line-height: 34px;
				}
			}
		}
	}
}