/* Navbar */

.navbar {
  font-family: $type1-semibold;
  background: $black;
  transition:background $action-transition-duration $action-transition-timing-function;
  -webkit-transition:background $action-transition-duration $action-transition-timing-function;
  -moz-transition:background $action-transition-duration $action-transition-timing-function;
  -ms-transition:background $action-transition-duration $action-transition-timing-function;
  .navbar-brand-wrapper {
    transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    -webkit-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    -moz-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    -ms-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    background: $sidebar-light-bg;
    .sidebar-dark & {
      background: $sidebar-dark-bg;
    }
    width: $sidebar-width-lg;
    height: $navbar-height;

    .navbar-brand {
      color: $white;
      font-size: 1.5rem;
      line-height: 48px;
      margin-right: 0;
      padding: .25rem 0;

      &:active,
      &:focus,
      &:hover {
        color: lighten(color(gray-dark), 10%);
      }

      img {
        width: calc(#{$sidebar-width-lg} - 130px );
        max-width: 100%;
        height: 28px;
        margin: auto;
        vertical-align: middle;
      }
    }

    .brand-logo-mini {
      display: none;
      img {
        width: calc(#{$sidebar-width-icon} - 50px );
        max-width: 100%;
        height: 28px;
        margin: auto;
      }
    }
  }

  .navbar-menu-wrapper {
    transition: width $action-transition-duration $action-transition-timing-function;
    -webkit-transition: width $action-transition-duration $action-transition-timing-function;
    -moz-transition: width $action-transition-duration $action-transition-timing-function;
    -ms-transition: width $action-transition-duration $action-transition-timing-function;
    color: $white;
    padding-left: 15px;
    width: calc(100% - #{$sidebar-width-lg});
    height: $navbar-height;
    @media (max-width: 991px) {
      width: auto;
    }

    .navbar-toggler {
      border: 0;
      color: inherit;
      &:not(.navbar-toggler-right) {
        @media (max-width: 991px) {
          display: none;
        }
      }
    }

    .search {
      width: 390px;
      margin-left: 1rem;
      .rtl & {
        margin-right: 1rem;
        margin-left: auto;
      }
      .input-group {
        padding: .25rem .5rem;
        background: color(gray);
        border-radius: .125rem;
        .input-group-append,
        input {
          background: transparent;
          border: 0;
          color: rgba($white, .5);
          font-size: .625rem;
          line-height: 1;
          @include input-placeholder {
            color: rgba($white, .5);
            font-size: .625rem;
          }
        }
        .input-group-append {
          color: rgba($white, .2);
          font-size: 1.25rem;
          i {
            &:before {
              line-height: 1.5;
            }
          }
        }
      }
    }

    .navbar-nav {
      flex-direction: row;
      align-items: stretch;

      .nav-item {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        @extend .d-flex;
        @extend .align-items-center;
        border-right: 1px solid color(gray);
        &:last-child {
          border-right: 0;
        }
        .nav-link {
          color: inherit;
          font-size: $navbar-font-size;
          @media (max-width: 767px) {
            margin-left: .5rem;
            margin-right: .5rem;
          }
          i {
            font-size: $navbar-icon-font-size;
          }
          &.nav-btn {
            .btn {
              background: rgba($white, .1);
              padding: .75rem 1rem;
              color: $white;
            }
            &:after {
              display: none;
            }
          }
        }
        &.nav-item-highlight {
          @extend .bg-primary-gradient;
        }
        &.dropdown {
          .dropdown-toggle {
            padding: 0;
            &:after {
              display: none;
            }
          }
          .count-indicator {
            position: relative;
            padding: 0;
            text-align: center;
            i {
              margin-right: 0;
              vertical-align: middle;
            }
            .count {
              position: absolute;
              width: 5px;
              height: 5px;
              border-radius: 100%;
              background: theme-color(danger);
              top: 2px;
              right: 0px;
            }
          }
          i {
            vertical-align: middle;
          }
          .navbar-dropdown {
            background: $navbar-dropdown-bg;
            font-size: .9rem;
            margin-top: 0;
            position: absolute;
            top: calc(#{$navbar-height} + 4px);
            right: 0;
            left: auto;
            .rtl & {
              right: auto;
              left: 0;
            }
            &.dropdown-left {
              left: 0;
              right: auto;
            }
            min-width: 100%;
            -webkit-box-shadow: 0px 0px 13px -3px rgba(0,0,0,0.10);
            -moz-box-shadow: 0px 0px 13px -3px rgba(0,0,0,0.10);
            box-shadow: 0px 0px 13px -3px rgba(0,0,0,0.10);
            @include border-radius(4px);
            @media (max-width: 991px) {
              right: -85px;
            }
            @extend .dropdownAnimation;
            border: 1px solid rgba(182, 182, 182, 0.1);
            padding: 0;
            overflow: hidden;
            .badge {
              margin-bottom: .75rem;
            }
            .dropdown-item {
              @extend .d-flex;
              @extend .align-items-center;
              margin-bottom: 0;
              padding: 14px 30px;
              width: calc(100% - 24px);
              background: $white;
              margin: 12px;
              border-radius: 6px;
              i {
                font-size: 18px;
                line-height: 34px;
              }

              .ellipsis {
                max-width: 200px;
                overflow: hidden;
                text-overflow: ellipsis;
              }

              .preview-icon {
                width: 34px;
                height: 34px;
                padding: 0;
              }
              
              .preview-item-content {
                flex-grow: 1;
                .preview-subject {
                  margin-bottom: .25rem;
                }
              }
            }
          }
        }
      }
      &.navbar-nav-right {
        @media (min-width: 992px) {
          margin-left: auto;
          .rtl & {
            margin-left: 0;
            margin-right: auto;
          }
        }
      }
    }
  }
  //navbar color variations
  &.navbar-light {
    background: theme-color(light);
    .navbar-toggler {
      color: $black;
    }
    .navbar-menu-wrapper {
      .navbar-nav {
        .nav-item {
          .nav-link {
            color: $black;
          }
        }
      }
    }
  }

  &.navbar-primary {
    background: theme-color(primary);

    .navbar-menu-wrapper {
      .navbar-nav {
        .nav-item {
          .nav-link {
            &.count-indicator {
              .count {
                background: theme-color(danger);
                color: $white;
              }
            }
          }
        }
      }
    }
  }

  &.navbar-success {
    background: theme-color(success);

    .navbar-menu-wrapper {
      input{
        color: $white;
        @include input-placeholder{
          color: inherit;
        }
      }
      .navbar-nav {
        .nav-item {
          .nav-link {
            &.count-indicator {
              .count {
                background: darken(theme-color(success), 30%);
                color: $white;
                border-color: theme-color(success);
                &.bg-success{
                  background: darken(theme-color(success), 20%) !important;
                }
              }
            }
          }
        }
      }
    }
  }

  &.navbar-danger {
    background: theme-color(danger);

    .navbar-menu-wrapper {
      input{
        color: $white;
        @include input-placeholder{
          color: inherit;
        }
      }
      .navbar-nav {
        .nav-item {
          .nav-link {
            &.count-indicator {
              .count {
                background: darken(theme-color(danger), 30%);
                color: $white;
                border-color: theme-color(danger);
                &.bg-danger{
                  background: darken(theme-color(danger), 20%) !important;
                }
              }
            }
          }
        }
      }
    }
  }

  &.navbar-warning {
    background: theme-color(warning);

    .navbar-menu-wrapper {
      input{
        color: $white;
        @include input-placeholder{
          color: inherit;
        }
      }
      .navbar-nav {
        .nav-item {
          .nav-link {
            &.count-indicator {
              .count {
                background: darken(theme-color(warning), 30%);
                color: $white;
                border-color: theme-color(warning);
                &.bg-warning{
                  background: darken(theme-color(warning), 20%) !important;
                }
              }
            }
          }
        }
      }
    }
  }

  &.navbar-pink {
    background: color(pink);
    a {
      color: $white;
    }
    .navbar-toggler {
      color: $white;
    }
    .navbar-brand-wrapper {
      .navbar-brand {
        color: $white;
      }
    }
    .navbar-menu-wrapper {
      input{
        color: $white;
        @include input-placeholder{
          color: inherit;
        }
      }
      .navbar-nav {
        .nav-item {
          .nav-link {
            color: $white;
            &.count-indicator {
              .count {
                background: darken(color(pink), 30%);
                color: $white;
                border-color: color(pink);
                &.bg-pink{
                  background: darken(color(pink), 20%) !important;
                }
              }
            }
          }
          &.dropdown {
            .navbar-dropdown{
              .dropdown-item{
                color: $black;
              }
            }
          }
        }
      }
    }
  }

  &.navbar-info {
    background: theme-color(info);

    a {
      color: $white;
    }
    .navbar-toggler {
      color: $white;
    }
    .navbar-menu-wrapper {
      input{
        color: $white;
        @include input-placeholder{
          color: inherit;
        }
      }
      .navbar-nav {
        .nav-item {
          color: $white;
          .nav-link {
            &.count-indicator {
              .count {
                background: darken(theme-color(info), 30%);
                color: $white;
                border-color: theme-color(info);
                &.bg-info{
                  background: darken(theme-color(info), 20%) !important;
                }
              }
            }
          }
          &.dropdown {
            .navbar-dropdown{
              .dropdown-item{
                color: $black;
              }
            }
          }
        }
      }
    }
  }

  &.navbar-danger,
  &.navbar-info,
  &.navbar-primary,
  &.navbar-success,
  &.navbar-warning,
  &.navbar-pink {
    .navbar-brand-wrapper {
      .navbar-brand {
        color: $white;
      }
    }

    .navbar-menu-wrapper {
      .navbar-nav {
        .nav-item {
          border-color: rgba($white, .7);
          &.nav-item-highlight {
            background: transparent;
          }
          .nav-link {
            color: $white;
          }
        }
      }
      .search {
        .input-group {
          background: $white;
          .input-group-append,
          input {
            @include input-placeholder{
              color: $black;
            }
          }
        }
      }
    }
    @extend .navbar-dark;
  }
  &.navbar-light {
    .navbar-menu-wrapper {
      .search {
        .input-group {
          background: rgba($black, .3);
          .input-group-append,
          input {
            @include input-placeholder{
              color: $black;
            }
          }
        }
      }
    }
  }
}
@media (max-width:991px) {
  .navbar {
    flex-direction: row;

    .navbar-brand-wrapper {
      width: 75px;

      .brand-logo {
        display: none;
      }

      .brand-logo-mini {
        display: inline-block;
      }
    }
  }

  .navbar-collapse {
    display: flex;
    margin-top: 0.5rem;
  }
}
